@import "utilities/variables";

.form {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .form-title {
        color: $base-color;
    }

    .form__element {
        display: flex;
        flex-direction: column;
        width: 590px;
        max-width: 100%;
        margin-bottom: 10px;

        label {
            color: grey;
        }

        input {
            border: 0;
            border-bottom: 1px solid grey;
            padding: 5px;

            &:focus {
                border-bottom: 1px solid $base-color;
            }
        }
    }

    button {
        margin: auto;
        padding: 5px 20px 5px 20px;
        background: 0;
        border: 1px solid grey;
        cursor: pointer;
        border-radius: 1px;

        &:hover {
            border: 1px solid $base-color;
            transition: all 0.2s;
        }
    }
}